import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

//引入文章(router懒加载)(导航页不加入循环)
const newestarticles = () => import ('../components/newestarticles')
const bod = () => import ('../components/body')
let length = 3;//文章数量
let articles = [];
for( let i = 0; i < length; i++ ){
    articles.push( { 'fn' : () => import ('../articles/a'+i) } );
}
//路由初始化
let mroutes = [{
        path: '/',
        component: bod
    }, {
        path: '/newest',
        component: newestarticles
},]
for( let j = 0; j < length; j++ ){
    mroutes.push({
        path: '/a'+j,
        component: articles[j].fn
    });
}
const router =  new Router({
    routes: mroutes
})
//路由监听
let Top = 0;
let nTop = 0;
router.afterEach(function(to,from,next){
    if(from.path == '/'){
        Top = window.scrollY;
        window.scrollTo(0,0)
    }
    if(to.path == '/'){
        setTimeout(function(){
            window.scrollTo(0,Top)
        },500)
    }
    if(from.path == '/newest'){
        nTop = window.scrollY;
        window.scrollTo(0,0)
    }
    if(to.path == '/newest'){
        setTimeout(function(){
            window.scrollTo(0,nTop)
        },500)
    }
})
export default router


