<template>
	<header>
		<transition name="showshade"><div class="shade" v-show="navshow" @click='navShow'></div></transition>
		<nav>
			<transition name="shownavlist">
				<ul class='navgationlist' v-show="navshow">
					<li v-for='(item,index) in navgationlist' @click='navShow'>
						<router-link :to='item.url'>{{item.title}}</router-link>
					</li>
				</ul>
			</transition>
			<transition name="showchangenavbtn">
				<a class="show_nav_btn" href="javascript:;" @click='navShow' v-show='!changenavbtn'>
					<i :class='navBtnIClasss.I1'></i>
					<i :class='navBtnIClasss.I2'></i>
					<i :class='navBtnIClasss.I3'></i>
				</a>
			</transition>
			<transition name="showchangenavbtn">
				<a class="show_nav_btn b2" href="javascript:;" @click='navShow' v-show='changenavbtn'>
					<i :class='navBtnIClasss.I4'></i>
					<i :class='navBtnIClasss.I5'></i>
					<i :class='navBtnIClasss.I6'></i>
				</a>
			</transition>
		</nav>
		<div id="blogname">
			<img :src="imgs.blogIcon" :alt="picLose">
			<a href='#/'>{{blogname}}</a>
		</div>
	</header>
</template>
<script>
	export default {
		name :'blogheader',
		data (){
			return{
				blogname:'尹先生的博客',
				picLose: "图片丢失",
				navshow: false,
				navBtnIClasss: {
					I1: "",
					I2: "",
					I3: "",
					I4: "",
					I5: "",
					I6: ""
				},
				imgs:{
					blogIcon: "http://img.52z.com/upload/news/image/20180623/20180623032709_55049.jpg",
				},
				navgationlist: [
					{
						title: '首页',
						url: '/'
					},{
						title: '更新',
						url: '/newest'
					},{
						title: '友连',
						url: '#'
					},{
						title: '归档',
						url: '#'
					},{
						title: '留言',
						url: '#'
					},
				]
			}
		},
		methods: {
			navShow: function(){
				var navshow = this.navshow;
				this.navshow = !navshow;
				if( !navshow ){
					this.navBtnIClasss.I1 = 'clockwiseRotate';
					this.navBtnIClasss.I2 = 'slideFadeToRight';
					this.navBtnIClasss.I3 = 'anticlockwiseRotate';
					this.navBtnIClasss.I4 = 'slideRight';
					this.navBtnIClasss.I5 = 'scaleOut';
					this.navBtnIClasss.I6 = 'slideLeft';
				}else{
					this.navBtnIClasss.I1 = 'clockwiseRotatereverse';
					this.navBtnIClasss.I2 = 'slideFadeFromRight';
					this.navBtnIClasss.I3 = 'anticlockwiseRotatereverse';
					this.navBtnIClasss.I4 = 'slideRightOut';
					this.navBtnIClasss.I5 = 'scaleIn';
					this.navBtnIClasss.I6 = 'slideLeftOut';
				}
			}
		},
		props: ['changenavbtn']
	};
</script>
<style scoped>
	header{ position: relative; width: 100%; height: 400px; background: rgba(0,0,0,.4); overflow: hidden; }
	.shade{ position: fixed; top: 0; left: 0; right: 0; bottom: 0; background: rgba(0,0,0,.4); z-index: 8; }
	nav{ position: fixed; top: 40px; right: 40px; display: flex; justify-content: flex-end; align-items: center; height: 24px; z-index: 9; z-index: 9999; }
	.navgationlist{ display: flex; padding-right: 40px; }
	.navgationlist li a{ display: block; width: 80px; text-align: center; font-size: 16px; font-weight: bold; color: #FFF; }
	.show_nav_btn{ position: absolute; width: 30px; height: 24px; padding: 5px; display: flex; flex-direction: column; justify-content: space-between; align-items: center; z-index: 1; }
	.show_nav_btn i{ width: 30px; height: 1px; background: #FFF; }
	.show_nav_btn.b2{ background: rgba(0,0,0,.5); padding: 5px; border-radius: 3px; flex-direction: row; }
	.show_nav_btn.b2 i{ width: 4px; height: 4px; border-radius: 50%; }

	#blogname{ position: absolute; top: 0; left: 0; width: 100%; height: 100%; display: flex; flex-direction: column; justify-content: center; align-items: center; }
	#blogname img{ width: 100px; border-radius: 50%; }
	#blogname a{ display: block; margin-top: 20px; text-align: center; font-size: 18px; color: #FFF; font-weight: bold; text-shadow: 0 2px 4px rgba(0,0,0,.1); }

	/*---动画---*/
	.shownavlist-enter-active {
		animation: slideFadeFromRight 1s;
	}
	.shownavlist-leave-active {
		animation: slideFadeToRight 1s;
	}
	.showchangenavbtn-enter-active {
		animation: scaleIn 1s;
	}
	.showchangenavbtn-leave-active {
		animation: scaleOut 1s;
	}
	.showshade-enter-active {
		animation: fadeIn 0.5s;
	}
	.showshade-leave-active {
		animation: fadeOut 0.5s;
	}
	/*---媒体查询---*/
	@media (max-width: 800px){
		header{ height: 300px; }
		header:after{ bottom: -270px; }
		nav{ top: 1em; right: 1em; }
		.navgationlist{ position: fixed; display: block; width: 60%; height: 100%; right: 0; top: 0; background: #333; padding-top: 100px; box-sizing: border-box; overflow-y: scroll; }
		.navgationlist li a{ width: auto; padding: 20px 40px; text-align: left; }
	}
</style>


