<template>
	<footer>
		<transition name="showFoote">
			<div class="footerWrap" v-show="showfooter">
				<div class="contactwrap">
					<i class="WeChat"></i>
					<i class="QQ"></i>
				</div>
		        <div id="busuanzi_container_site_pv">本站总访问量<span id="busuanzi_value_site_pv"></span>次</div>
	        </div>
		</transition>
	</footer>
</template>
<script>
	export default{
		name: 'foot',
		data () {
            return {
            }
        },
        props :['showfooter']
	};
</script>
<style scoped>
	footer{ position: relative; width: 100%; height: 400px; background: rgba(0,0,0,.4); overflow: hidden; }
	.footerWrap{ display: flex; width: 100%; height: 100%; flex-direction: column; align-items: center; justify-content: center; }
	.contactwrap{ display: flex; justify-content: center; }
	.contactwrap i{ display: block; width: 80px; height: 80px; margin: 0 20px; background: #FFF; }
	.contactwrap i.WeChat{ background: url(../assets/WeChat.png); background-size: 100%; }
	.contactwrap i.QQ{ background: url(../assets/QQ.png); background-size: 100%; }
	#busuanzi_container_site_pv{ color: #FFF; margin-top: 20px; }

	.showFoote-enter-active{
        -webkit-animation: scaleIn 1s;
        animation: scaleIn 1s;
	}
	.showFoote-leave-active {
        -webkit-animation: scaleOut 1s;
        animation: scaleOut 1s;
	}
	/*---媒体查询---*/
	@media (max-width: 800px){
		footer{ height: 300px; }
		footer:after{ top: -270px; }
	}
</style>


